import React from 'react'

const Contact = () => {
  return (
    <>
        <div className='contact mt-5'>
          <h3 className='text-center'>Contact US</h3>
        </div>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12'>
              <div className='row'>
                <div className='col-md-4 col-12 mx-auto mt-3'>
                <form className="row g-3">
                  <div className="col-md-12">
                    <label for="nameid" className="form-label">Name</label>
                    <input type="name" className="form-control" id="nameid" />
                  </div>
                  <div className="col-md-12">
                    <label for="inputPassword4" className="form-label">Mobile Number</label>
                    <input type="password" className="form-control" id="inputPassword4" />
                  </div>
                  <div className="col-md-12">
                    <label for="inputPassword4" className="form-label">Email Id</label>
                    <input type="password" className="form-control" id="inputPassword4" />
                  </div>
                  <div className="col-12">
                    <label for="inputAddress2" className="form-label">Address 2</label>
                    <textarea class="form-control" aria-label="With textarea"></textarea>
                  </div>
                  <div className="col-12">
                    <button type="submit" className="btn btn_get_started">Submit</button>
                  </div>
                </form>
                </div>
              </div>
            </div>
          </div>
        </div>
    </>
  )
}

export default Contact