import React from "react";


const Card = (props) => {
  return (
    <>
      <div className="col-md-4 mx-auto mt-5">
        <div className="card">
          <img src={props.Cdata.imgsrc} className="card-img-top img-fluid" alt="..." />
          <div className="card-body">
            <h5 className="card-title">{props.Cdata.title}</h5>
            <p className="card-text text-muted">
              Some quick example text to build on the card title and make up the
              bulk of the card's content.
            </p>
            <a href="#" className="btn btn_get_started ">
              Click Here
            </a>
          </div>
        </div>
      </div>
    </>
  );
};

export default Card;
